:root {
  --font-inter: "EB Garamond", serif;
}

:root {
  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(37, 97%, 70%);
  --instagram-end: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --toggle-bg-start: hsl(210, 78%, 56%);
  --toggle-bg-end: hsl(146, 68%, 55%);
  --toggle: hsl(230, 22%, 74%);
  --dark-bg: hsl(230, 17%, 14%);
  --dark-top-bg: hsl(232, 19%, 15%);
  --dark-card: hsl(228, 28%, 20%);
  --dark-card-hover: hsl(230, 34%, 24%);
  --dark-text1: hsl(0, 0%, 78%);
  --dark-text2: hsl(0, 0%, 100%);
  --light-bg: hsl(0, 0%, 100%);
  --light-top-bg: hsl(225, 100%, 98%);
  --light-card: hsl(227, 47%, 96%);
  --light-text1: hsl(0, 0%, 1%);
  --light-text2:hsl(230, 17%, 14%);
}

@media (prefers-color-scheme: light) {
  :root {
    --dark-bg: hsl(0, 0%, 100%);
    --dark-top-bg: hsl(225, 100%, 98%);
    --dark-card: hsl(227, 47%, 96%);
    --dark-card-hover: hsl(229, 14%, 77%);
    --dark-text1: hsl(240, 5%, 16%);
    --dark-text2: hsl(230, 17%, 14%);
    --light-bg: black;
    --toggle-bg-start: hsl(0, 0%, 49%);
    --toggle-bg-end: hsl(0, 0%, 49%);
  }
}
body.light {
  --dark-bg: hsl(0, 0%, 100%);
  --dark-top-bg: hsl(225, 100%, 98%);
  --dark-card: hsl(227, 47%, 96%);
  --dark-card-hover: hsl(229, 14%, 77%);
  --dark-text1: hsl(240, 5%, 16%);
  --dark-text2: hsl(230, 17%, 14%);
  --light-bg: black;
  --toggle-bg-start: hsl(0, 0%, 49%);
  --toggle-bg-end: hsl(0, 0%, 49%);
}

body.dark {
  --dark-bg: hsl(230, 17%, 14%);
  --dark-top-bg: hsl(232, 19%, 15%);
  --dark-card: hsl(228, 28%, 20%);
  --dark-card-hover: hsl(230, 34%, 24%);
  --dark-text1: hsl(0, 0%, 78%);
  --dark-text2: hsl(0, 0%, 100%);
  --light-bg: hsl(0, 0%, 100%);
  --light-top-bg: hsl(225, 100%, 98%);
  --light-card: hsl(227, 47%, 96%);
  --light-text1: hsl(0, 0%, 1%);
  --light-text2:hsl(230, 17%, 14%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background: var(--dark-bg);
  color: var(--dark-text2);
}

@keyframes fadeInAnimation {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
h1, h2, h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container1 {
  margin: 2.5rem -0.625rem 2.875rem;
  max-width: 120rem;
  display: grid;
  grid-template-columns: auto auto auto auto;
  justify-content: space-evenly;
}
@media (min-width: 71.875em) {
  .container1 {
    gap: 1.875rem;
  }
}
@media (max-width: 71.125em) {
  .container1 {
    display: grid;
    grid-template-columns: auto auto;
  }
}
@media (max-width: 39.9375em) {
  .container1 {
    display: grid;
    grid-template-columns: auto;
    padding: 0;
  }
}

.header {
  opacity: 0;
  margin-top: 3.125rem;
  margin-left: 3.125rem;
  animation: fadeInAnimation ease 3s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-delay: 0.2s;
}
@media (min-width: 40em) {
  .header {
    display: flex;
    justify-content: space-between;
  }
}
@media (prefers-color-scheme: light) {
  .header {
    text-shadow: none;
  }
}
@media (max-width: 39.9375em) {
  .header {
    margin-left: 0rem;
  }
}
.header__subtitles {
  font-size: 1.75rem;
  font-weight: 700;
  color: var(--dark-text-1);
}
.header__toggle {
  position: relative;
  height: 4.849375rem;
  width: 13.32625rem;
}
@media (max-width: 39.9375em) {
  .header__toggle {
    margin-top: 1.875rem;
  }
}

.legend {
  margin-left: 3.4375rem;
  font-weight: bold;
}
@media (max-width: 39.9375em) {
  .legend {
    justify-content: center;
    margin-left: 2.8125rem;
  }
}

.toggle {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(2, 1fr);
  border: 0px black dashed;
  gap: 0.01875rem 0.75rem;
}
@media (max-width: 39.9375em) {
  .toggle {
    align-items: center;
    padding-left: 2.5rem;
  }
}
.toggle label[for=dark] {
  grid-column: 1/2;
  justify-self: end;
}
.toggle label[for=system] {
  grid-column: 2/3;
  grid-row: 2/3;
  justify-self: center;
}
.toggle label[for=light] {
  grid-column: 3/4;
  justify-self: left;
}
.toggle__wrapper {
  position: relative;
  grid-column: 2/3;
  grid-row: 1/2;
  height: 1.5rem;
  width: 3.4375rem;
  top: 0.3125rem;
}
.toggle input[type=radio] {
  opacity: 1;
  margin: 0;
  width: 1rem;
  height: 1rem;
  bottom: 0.5rem;
}
.toggle__background {
  height: 100%;
  display: block;
  position: absolute;
  width: 100%;
  left: -0.0625rem;
  bottom: 0.25rem;
  border-radius: 0.75rem;
  background: linear-gradient(225deg, var(--toggle-bg-end) 0%, var(--toggle-bg-start) 98.02%);
  pointer-events: none;
}
.toggle__light {
  position: absolute;
  left: 2.1875rem;
  bottom: -1.25rem;
}
.toggle__button {
  position: absolute;
  width: 1.125rem;
  height: 1.125rem;
  border-radius: 100%;
  border: 0.125rem solid;
  background-color: var(--dark-bg);
  left: 0.125rem;
  top: -0.0625rem;
  opacity: 1;
  transition: all 150ms ease-in-out;
  pointer-events: none;
}
@media (prefers-color-scheme: light) {
  .toggle__button {
    border: none;
  }
}
.toggle #light:checked ~ .toggle__button {
  left: calc(100% - 1.25rem);
}
.toggle #system:checked ~ .toggle__button {
  left: 33%;
}

.card {
  position: relative;
  background: var(--dark-card);
  color: var(--dark-text1);
  width: 15.9375rem;
  height: 13.5rem;
  padding-top: 1.5rem;
  text-align: center;
  border-radius: 0.25rem;
  transition: background 150ms ease-in-out;
}
@media (max-width: 71.125em) {
  .card {
    margin-bottom: 3.75rem;
  }
}
.card:hover {
  background: var(--dark-card-hover);
}
.card__icon {
  padding-left: 3.125rem;
}
.card__icon--small {
  padding-right: 2.1875rem;
}
.card__facebook {
  border-top: 0.25rem solid var(--facebook);
  opacity: 0;
  animation: fadeInAnimation ease 3s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
}
.card__twitter {
  border-top: 0.25rem solid var(--twitter);
  opacity: 0;
  animation: fadeInAnimation ease 3s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-delay: 0.2s;
}
.card__instagram {
  opacity: 0;
  animation: fadeInAnimation ease 3s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-delay: 0.4s;
}
.card__instagram::before {
  background: linear-gradient(225deg, var(--instagram-start) 0, var(--instagram-end) 98.02%);
  border-top-left-radius: 0.1875rem;
  border-top-right-radius: 0.1875rem;
  content: "";
  display: block;
  height: 0.3125rem;
  position: absolute;
  top: 0;
  width: 100%;
}
.card__youtube {
  border-top: 0.25rem solid var(--youtube);
  opacity: 0;
  animation: fadeInAnimation ease 3s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-delay: 0.6s;
}
.card__youtube:hover {
  background: var(--dark-card-hover);
}
.card__top {
  display: flex;
  justify-content: space-between;
}
.card__bottom {
  display: flex;
  justify-content: space-between;
}
.card__platform {
  display: flex;
}
.card__small {
  opacity: 0;
  display: block;
  flex-direction: row;
  background: var(--dark-card);
  color: var(--dark-text1);
  width: 15.9375rem;
  height: 7.25rem;
  padding-top: 1.5rem;
  border-radius: 0.25rem;
  animation: fadeInAnimation ease 3s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-delay: 1.4s;
}
@media (max-width: 71.125em) {
  .card__small {
    margin-bottom: 3.75rem;
  }
}
.card__small__platform {
  display: flex;
}
.card__small:hover {
  background: var(--dark-card-hover);
}
.card__username {
  padding-left: 1.25rem;
  font-weight: bold;
  font-size: 1rem;
}
.card__count--big {
  padding-top: 1.125rem;
  font-size: 2.5rem;
  font-weight: bold;
  color: var(--light-bg);
}
.card__count--small {
  font-size: 1.25rem;
  font-weight: bold;
  padding-right: 1.875rem;
  padding-top: 0.9375rem;
  color: var(--light-bg);
}
.card__label {
  color: var(--dark-text-1);
  font-size: 0.875rem;
  letter-spacing: 0.3125rem;
}
.card__label--small {
  color: var(--dark-text-1);
  font-size: 1rem;
  font-weight: bold;
  top: 0.625rem;
  padding-left: 1.875rem;
}
.card__change--up {
  padding-top: 1.25rem;
  font-size: 0.875rem;
  font-weight: bold;
  color: var(--limegreen);
}
.card__change--up--small {
  padding-top: 1.25rem;
  padding-left: 1.875rem;
  font-size: 0.875rem;
  font-weight: bold;
  color: var(--limegreen);
}
.card__change--down {
  padding-top: 1.25rem;
  font-size: 0.875rem;
  font-weight: bold;
  color: var(--brightred);
}
.card__change--down--small {
  padding-top: 1.25rem;
  padding-left: 1.875rem;
  font-size: 0.875rem;
  font-weight: bold;
  color: var(--brightred);
}