

html{
    font-size: 100%;
    box-sizing: border-box;
}

*,
 *::before, 
 *::after{
    box-sizing: inherit;

}

body{
    margin: 0;
    padding: 0;
    font-family: var(--font-inter);
    background: var(--dark-bg);
    color: var(--dark-text2);
}
 
@keyframes fadeInAnimation {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
     }
}

